<template>
  <div id="app">
    <!-- {{this.$store.state.name}} -->
    <todo-header></todo-header>
    <router-view></router-view>
    <!-- <todo-footer></todo-footer> -->
  </div>
</template>

<script>

import Vue from 'vue';
import VueRouter from 'vue-router';
import TodoHeader from './components/TodoHeader.vue';
import TodoInput from './components/TodoInput.vue';
//import TodoFooter from './components/TodoFooter.vue';
import TodoList from './components/TodoList.vue';
import TodoDetail from './components/TodoDetail.vue';
import TodoUpdate from './components/TodoUpdate.vue';


Vue.use(VueRouter);//VueRouter전역사용 위해.
//VueRouter(플러그인)은 일반적으로 전역 수준 기능을 Vue에 추가
//플러그인은 여러 컴포넌트에서 사용되는(특히 전역) 기능을 만들 때 사용

const router = new VueRouter({
  mode : 'history',
  routes : [
    {path:'/',component:TodoList},
    {path:'/list',component:TodoList},
    {path:'/input',component:TodoInput},
    {path:'/detail/:num',component:TodoDetail},
    {path:'/update',component:TodoUpdate}
  ]
});

export default {
  name: 'app',
  components: {
    TodoHeader,//router 화면만
  },
  created(){
    this.$router.push('/');
  },
  
  router,
  data() {
    return {
      todolist: [],
      todo : {}
    }
  },
}
</script>

