<template>
  <div>
    <table>
      <tbody>
        <tr>
          <td>UPDATE번호</td>
          <td>
              {{todo.num}}
          </td>
        </tr>
        <tr>
          <td>등록일</td>
          <td>{{todo.wdate}}
            <!-- <input type="text" v-model="todo.todo" /> -->
          </td>
        </tr>
        <tr>
          <td>제목</td>
          <td>
            <input type="text" v-model="todo.title"/>
          </td>
        </tr>
        <tr>
          <td>내용</td>
          <td>
            <input type="text" v-model="todo.content" />
          </td>
        </tr>
        <tr>
          <td>조회수</td>
          <td>
           {{todo.count}}
          </td>
        </tr>        
      </tbody>
    </table>
    <button class="addbutton" v-on:click="update"> 수정완료</button>
    
  </div>
</template>

<script>
import Constant from "../js/Constant";
export default {
    data : function() {
        return { 
            num : "",
            title : "",
            content: ""
            }
    },
  created() {
    console.log('',this.$route.params.num);
    
    this.getTodo(this.$route.params.num);
  },
  computed: {

    todo() {
        // console.log(this);
      return this.$store.state.todo;
      }
    },
  methods: {
      update(){
            this.$store.dispatch(Constant.UPDATE_TODO, { todo: this.todo});
            this.$router.push('/list');
         },

         
    // getTodo(num) {      
    //   this.$store.dispatch(Constant.SHOW_TODO, {num: num });
    // },
    
    clear() {
      //this.todo = {}; //this.todo = {} 이렇게 하면 안나옴
    }
  }
};
</script>

