<style>
* {  box-sizing: border-box;  }
.input {
    border: none; width: 75%; height:35px; padding: 10px;
    float: left; font-size: 16px;
}
.addbutton {
    padding: 10px; width: 25%; height:35px; background: #4861cf; 
    color: rgb(212, 240, 235); float: center; text-align: center;
    font-size: 13px; cursor: pointer; transition: 0.3s;
}
.addbutton:hover { background-color:#505a83; }

.textarea {
    border: none;  padding: 10px;
    float: left; font-size: 16px; resize: none;
}
</style>
<template>
    <div>
        <!-- <input class="input" type="text" id="task" v-model.trim="todo" 
            placeholder="입력 후 엔터!" v-on:keyup.enter="addTodo">     -->
       <input v-model ="name" placeholder="글쓴이" /><br><br>
       <input v-model ="title" placeholder="제목" /><br><br>
       <textarea cols="50" rows="10" v-model ="content" placeholder="내용" /><br><br>
       
        <span class="addbutton" v-on:click="write">작 성</span>
    </div>    
</template>

<script type="text/javascript">
import Constant from '../js/Constant'

export default {
    name : 'input-todo',
    data : function() {
        return { 
            todo : "",
            name : "",
            title : "",
            content: ""
            }
    },
    methods : {
        addTodo : function() {
                        
            this.$store.dispatch(Constant.ADD_TODO, { todo: this.todo });
            this.todo = "";
            this.$router.push('/list');
        },

        write(){
            this.$store.dispatch(Constant.ADD_TODO, { todo: this.todo, name: this.name,
            title: this.title, content: this.content });
           
            this.$router.push('/list');
         }
    }
}
</script>