<style>
* {
  box-sizing: border-box;
}
ul {
  margin: 0;
  padding: 0;
}
ul li {
  cursor: pointer;
  position: relative;
  padding: 8px 8px 8px 40px;
  background: #eee;
  font-size: 14px;
  transition: 0.2s;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
ul li:hover {
  background: #ddd;
}
ul li.checked {
  background: #bbb;
  color: #fff;
  text-decoration: line-through;
}
ul li.checked::before {
  content: "";
  position: absolute;
  border-color: #fff;
  border-style: solid;
  border-width: 0px 1px 1px 0px;
  top: 10px;
  left: 16px;
  transform: rotate(45deg);
  height: 8px;
  width: 8px;
}
.close {
  position: absolute;
  right: 0;
  top: 0;
  padding: 8px 12px 8px 12px;
}
.close:hover {
  background-color: #f44336;
  color: white;
}
.removeBtn {
  margin-left: auto;
  color: #de4343;
}
.linethru {
  text-decoration: line-through;
}

.checkBtn {
  line-height: 45px;
  color: #62acde;
  margin-right: 5px;
}
.result{
    height: 20px;
    
    margin: 20px;
}
</style>
<template>
  <div>
    <!-- <div v-if="todo" class="result">
        번호: {{todo.num}} / 할일: {{todo.todo}} / 시작일: {{todo.wdate}} 
        /완료일: {{todo.edate}} / 완료여부: {{todo.done == 'Y' ? '완료':'미완료' }}        
    </div>    -->
    <ul>
      <li v-for="one in todolist" :key="one.num"  @click.stop="show( one.num )">
        <span >&#x1F60E;</span>
        <span > {{one.num}}. {{ one.title }}</span>
        <span class="close" @click.stop="deleteTodo({ num: one.num })">&#x00D7;</span>
        
      </li>
    </ul>
  </div>
</template>
<script type="text/javascript">
import Constant from "../js/Constant";

export default {
  name: "ListTodo",
  created() {    
    this.$store.dispatch(Constant.ALL_TODO); //처음에 data get
    console.log(this.todolist);
    
  },
  
  computed: {
    todolist() {
      return this.$store.state.todolist;
    },
    todo(){
        return this.$store.state.todo;
    }
  },
  methods: {
    completed: function(payload) {
        this.$store.dispatch(Constant.COMPLETE_TODO, payload);
    },

    deleteTodo: function(payload) {
      this.$store.dispatch(Constant.DELETE_TODO, payload);
    },
    completeTodo: function(payload) {

      this.$store.dispatch(Constant.COMPLETE_TODO, payload); //action call
    },
    show(num) {
        console.log(num);
        this.$router.push('/detail/'+num);
     // this.$store.dispatch(Constant.SHOW_TODO, payload);
    },
  },
};
</script>
