import Vue from "vue";
import Vuex from "vuex";

import Constant from "./Constant.js";
import axios from "./axios-common.js";

//전역에서 vuex사용 가능하도록 선언
Vue.use(Vuex);

const store = new Vuex.Store({

  state: {//data
    todolist: [], //서버에서 받아온 데이터 저장할 배열    
    name: "HappyHouse QnA",
    todo:null
  },
  //method: 비동기 호출(순서 보장 안되고 동시 실행 가능한 작업)
  actions: {
    //all***axios 요청 후 결과가 오면 mutation(store.commit)에 지시해서 state 변경 요청
      [Constant.ALL_TODO]: store => {
          axios
          .get("/board")
          .then(response => { //직접 데이터 처리 안됨
              store.commit(Constant.ALL_TODO, { todolist: response.data });
              console.log('haha',response.data); })//서버에서 받은 데이터로 todolist 셋팅해줌            
          .catch(exp => alert("getTodoList처리에 실패하였습니다." + exp));
      },

      [Constant.ADD_TODO]: (store, payload) => {
          console.log("###addTodo!!!", payload);
          
          axios.post("/board", {
                  name: payload.name,
                title: payload.title,
              content: payload.content}) //서버에 요청. db insert
                  .then(() => {
                  console.log("추가하였습니다.");
                  store.dispatch(Constant.ALL_TODO);//다시 액션에 새데이터 FETCH 요청
                  })
                  .catch(exp => alert("추가 처리에 실패하였습니다." + exp)); 
      },    
    //ADD_TODO
    [Constant.DELETE_TODO] : (store, payload) => {
              console.log("###deleteTodo!!!", payload.id);
              //store.commit(Constant.DELETE_TODO, payload);
              axios.delete('/board/' + payload.num)
                  .then(() => {
                      console.log('삭제하였습니다.');
                      store.dispatch(Constant.ALL_TODO);
                  })
                  .catch(exp => alert('삭제 처리에 실패하였습니다.' + exp)); 
      },
  
      [Constant.COMPLETE_TODO]: (store, payload) => {
          axios.put('/board/' + payload.num)
              .then(() => {
                  console.log('완료 처리하였습니다.');
                  store.dispatch(Constant.ALL_TODO);
              })
              .catch(exp => alert('완료 처리에 실패하였습니다.' + exp)); 
      },

      [Constant.DELETEALL_TODO]: (store) => {
        axios.delete('/board')
            .then(() => {
                console.log('완료 처리하였습니다.');
                store.dispatch(Constant.ALL_TODO);
            })
            .catch(exp => alert('삭제 처리에 실패하였습니다.' + exp)); 
    },

      [Constant.SHOW_TODO]: (store, payload) => {
        axios.get('/board/' + payload.num)
            .then((response) => {
                console.log('조회 처리하였습니다.', response.data);
                store.commit(Constant.SHOW_TODO, { todo:response.data });
            })
            .catch(exp => alert('조회 처리에 실패하였습니다.' + exp)); 
    },
    // // put
    [Constant.UPDATE_TODO]: (store, payload) => {
      //console.log("###addTodo!!!", payload);
      
      axios.put("/board", {
              todo: payload.todo, num: payload.todo.num, title: payload.todo.title, content: payload.todo.content}) //서버에 요청. db insert
              .then(() => {
              console.log("수정하였습니다.");
              store.dispatch(Constant.ALL_TODO);//다시 액션에 새데이터 FETCH 요청
              })
              .catch(exp => alert("수정 처리에 실패하였습니다." + exp)); 
    },

  },//ACTIONS

  mutations: {
    [Constant.ALL_TODO]: (state, payload) => {
      
      state.todolist = payload.todolist;
      console.log('kkk',state.todolist);
    },
     [Constant.SHOW_TODO]: (state, payload) => { 
         console.log(payload);             
         state.todo = payload.todo;       
     },
   /*
     [Constant.COMPLETE_TODO]: (state, payload) => {
      var index = state.todolist.findIndex(item => item.id === payload.id);
      if (payload.completed == "false") {
        state.todolist[index].completed = "true";
      }  else {
        state.todolist[index].done = "true";
      } 
    },*/
    /*
    [Constant.DELETE_TODO]: (state, payload) => {
      var index = state.todolist.findIndex(item => item.id === payload.id);
      state.todolist.splice(index, 1);
    } */
  }

});
    /* [Constant.ALL_TODO] : (store) => {
            console.log("###allTodo!!!");
            store.commit(Constant.ALL_TODO);
        },
        
        [Constant.DELETE_TODO] : (store, payload) => {
            console.log("###deleteTodo!!!", payload);
            store.commit(Constant.DELETE_TODO, payload);
        },
        [Constant.DONE_TOGGLE] : (store, payload) => {
            console.log("###doneToggle!!!", payload);
            store.commit(Constant.DONE_TOGGLE, payload);
        } */
  /* { id:1, todo : "영화보기", done:'N' },
            { id:2, todo : "주말 산책", done:'Y' },
            { id:3, todo : "ES6 학습", done:'N' },
            { id:4, todo : "잠실 야구장", done:'N' }, */


export default store;
